.container{height:100%;display:flex;flex-direction:column}.container .logo{width:50px;height:50px;margin:8px 0 12px 8px}.container .logo .acLogo{width:40px;height:40px}.container .navItem{position:relative;flex:1}.container .navItem ul{padding:0}.container .navItem .bottomItem ul li{position:absolute;bottom:0;margin-bottom:0}.overError,.emptyError{width:100%}.overError::after,.emptyError::after{content:"";font-size:15px;font-weight:500;color:var(--error)}.emptyError::after{content:"內容不可空白";position:absolute;right:10px;bottom:2px}.overError::after{content:"字數不可超過140字";position:absolute;right:10px;bottom:2px}