.container{width:639px;height:335px;margin-top:16px;border-bottom:1px solid var(--border)}.container .wrapper{display:flex;flex-direction:column;width:602px;height:100%;padding:0 16px}.top{width:100%;height:205px}.top .info{display:flex}.top .info .avatar{width:50px;height:50px;margin-right:8px;object-fit:cover;border-radius:50%;cursor:pointer}.top .info .avatar:hover{box-shadow:0px 0px 0px 5px rgba(255,102,0,.55)}.top .info .title{display:flex;flex-direction:column;margin-top:8px}.top .info .title .name{font-size:16px;font-weight:700;color:var(--grayscale-dark-100);margin:8px 0 4px 0}.top .info .title .acount{font-size:14px;color:var(--secondary)}.top .text{width:578px;height:108px;margin:8px 0;font-size:24px;color:var(--grayscale-dark-100);line-height:25px}.top .date{font-size:14px;font-weight:500;color:var(--secondary)}.bottom{position:relative;display:flex;flex-direction:column;width:602px;height:130px}.bottom::before{content:"";position:absolute;width:100%;height:1px;background-color:var(--border)}.bottom::after{content:"";position:absolute;top:50%;width:100%;height:1px;background-color:var(--border)}.bottom .showBox{display:flex;justify-content:space-between;align-items:center;width:210px;height:28px;margin:16.5px 0}.bottom .showBox .showReply,.bottom .showBox .showLikes{display:inline-block;font-size:19px;font-weight:500;color:var(--secondary)}.bottom .showBox span{color:var(--grayscale-dark-100)}.bottom .icon{display:flex;justify-content:space-between;width:188px;height:36px;margin:16.5px 0;padding:3px 0}.bottom .icon img{width:30px;height:30px;cursor:pointer}.overError,.emptyError{position:absolute;bottom:43px;right:-80px;height:2px;width:50%}.overError::after,.emptyError::after{content:"";font-size:15px;font-weight:500;color:var(--error)}.emptyError::after{content:"內容不可空白";position:absolute;right:185px}.overError::after{content:"字數不可超過140字"}