.container{width:639px;height:153px;padding:8px 0;border-bottom:1px solid var(--border);cursor:pointer}.container:hover{background-color:var(--grayscale-dark-30)}.container .wrapper{display:flex;width:100%;height:136px;padding:0 24px}.container .avatar{width:50px;height:50px;object-fit:cover;border-radius:50%}.container .avatar:hover{box-shadow:0px 0px 0px 5px rgba(255,102,0,.55)}.container .tweet{padding-left:13px}.container .title{display:flex;margin-top:8px}.container .title .name{font-size:16px;font-weight:700;color:var(--grayscale-dark-100);margin-right:8px;white-space:nowrap;overflow:hidden;text-overflow:ellipsis}.container .title .acount{font-size:14px;font-weight:400;color:var(--secondary)}.container .text{width:528px;height:78px;margin:8px 0;font-size:16px;font-weight:400;line-height:26px;color:var(--grayscale-dark-100)}.container .bottom{display:flex;justify-content:space-between;width:120px;height:16px img;height-width:16px;height-height:16px}.container .bottom span{font-size:14px;font-weight:600;color:var(--secondary);margin-left:8px}.container .bottom .reply,.container .bottom .like{display:flex}.container .bottom img:hover{transform:scale(1.5)}.overError,.emptyError{position:absolute;bottom:43px;right:-80px;height:2px;width:50%}.overError::after,.emptyError::after{content:"";font-size:15px;font-weight:500;color:var(--error)}.emptyError::after{content:"內容不可空白";position:absolute;right:185px}.overError::after{content:"字數不可超過140字"}