.container{display:flex;flex-direction:column;align-items:center;margin-top:64px}.title{margin:24px auto 40px auto;color:#1c1c1c}.loginButton{margin-top:16px}.otherLink{width:356px;display:flex;justify-content:flex-end;margin-top:16px}.otherLink .register,.otherLink .admin{font-size:16px;color:var(--primary);text-decoration:underline;background-color:#fff;cursor:pointer}.otherLink .dot{font-size:16px;padding:0 5px}.passwordInfoError,.passwordLengthError,.accountInfoError,.accountLengthError{background-color:var(--error)}.passwordInfoError::after,.passwordLengthError::after,.accountInfoError::after,.accountLengthError::after{content:"";position:absolute;left:0;color:var(--error);font-size:12px;line-height:20px;font-weight:500}.accountLengthError::after{content:"請輸入帳號!"}.accountInfoError::after{content:"此帳號不存在!"}.passwordLengthError::after{content:"請輸入密碼!"}.passwordInfoError::after{content:"密碼錯誤!"}