.header{width:639px;height:59px;margin-top:16px;padding:9px 0 0 24px;border-bottom:1px solid var(--border)}.input{position:relative}.nameError{background-color:var(--error)}.nameError::after{content:"字數超過上限!";position:absolute;bottom:-15px;left:0;color:var(--error);font-size:12px;font-weight:500}.passwordError{background-color:var(--error)}.passwordError::after{content:"密碼不一致!";position:absolute;bottom:-15px;left:0;color:var(--error);font-size:12px;font-weight:500}.btn{position:absolute;right:24px}.loading{position:absolute;top:30%;right:50%;display:flex;flex-direction:column;justify-content:center}@keyframes Loading-Icon{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}@media(prefers-reduced-motion: no-preference){.loading .loadingIcon{animation:Loading-Icon 3s linear infinite}}.loading .loadingText{font-size:20px;font-weight:700;color:var(--brand)}