.container {
  width: 639px;
  height: 133px;
  display: flex;
  padding: 0 20px;
  margin-top: 16px;
  border-bottom: 1px solid #e6ecf0;
}

.avatar {
  width: 50px;
  height: 50px;
  margin-right: 13px;
  object-fit: cover;
  border-radius: 50%;
  cursor: pointer;
  &:hover {
    box-shadow: 0px 0px 0px 5px rgba(255, 102, 0, 0.55);
  }
}

.replyInfo {
  margin-left: 5px;
  padding-top: 3px;

  .userInfo {
    margin-bottom: 15px;
    .userName {
      font-size: 16px;
      font-weight: 700;
      color: var(--grayscale-dark-100);
      // white-space: nowrap;
      // overflow: hidden;
      // text-overflow: ellipsis;
    }

    .userAcount {
      font-size: 14px;
      color: var(--secondary);
      padding-left: 8px;
    }
  }

  .replyNameWrapper {
    margin-bottom: 15px;

    .replyTitle {
      font-size: 14px;
      color: var(--secondary);
    }

    .replyName {
      font-size: 14px;
      color: var(--brand);
      padding-left: 3px;
    }
  }

  .replyMessageText {
    font-size: 16px;
    color: var(--grayscale-dark-100);
  }
}
